.contacts {
	background: url(../img/bottom_bg.jpg) no-repeat center;
	background-size: cover;
	color: #fff;
	position: relative;
	padding-top: 55%;
	padding-bottom: 7%;
	.h2_title {
		
	}

	& > * {
		position: relative;
		z-index: 2;
	}
}

.contacts_form {
	.flex();
	width: 100%;
	margin-bottom: 70px;
}

input.main-btn {
	color: #fff;
	width: 100%;
	max-width: 440px;
	height: 70px;
	cursor: pointer;
}

.text-field {
	width: 100%;
	margin-right: 15px;
    max-width: 375px;
    height: 70px;
	text-align: left;
    padding: 15px 45px;
    text-transform: uppercase;
    font-family: 'Exo 2',sans-serif;
    font-size: 16px;
    &::-webkit-input-placeholder{
    	color: #000;
    }
	
    &::-moz-placeholder {
    	color: #000;
    }
}

.contacts_h3 {
	font-size: (60vw/1280*100);
	font-weight: 500;
	margin-bottom: 70px;
}

.contasts_tel, .contacts_mail {
	font-size: (30vw/1280*100);
	font-weight: 500;
}

.contacts_wrapper {
	.flex();
}

.contacst_contact {
	width: 100%;
}

.contacts_wrap {
	display: inline-block;
	.section_subtitle {
		text-align: right;
	}
}

.contacts_wrapper {
	max-width: 1148px;
}