.myPopup {
	max-width: 745px;
	margin: 0 auto;
	width: 100%;
	.main-btn {
		color: #fff;
	}

	.center {
		background: #f6f6f6;
		background-size: cover;
			padding-top: (138%/740*100);
		padding-bottom: (109%/740*100);
		padding-left: (86%/740*100);
		padding-right: (86%/740*100);
		text-align: center;
		color: #000;
	}

	.popup_form {
		.footer_btn {
			margin-top: 12px;
		}
		.flex();
		flex-direction: column;
		justify-content: center;
		width: 100%;
		.text-field {
			border: none;
			max-width: 100%;
			text-align: center;
			
			width: 100%;
			color: #000;
			&::-webkit-input-placeholder {
    		color: #000;
    	
    }

     &::-moz-input-placeholder {
    	color: #000;
    }

    & {
			margin-bottom: 40px;
    	}
		}
	}

	
}


.popup_title {
	margin-bottom: 17px;
	&:before {
		content: none;
	}
}


.popup_description {
	margin-bottom: 30px;
	font-size: 16px;
}

.drinks_popup {
	.popup_img-layer {
		margin: 0 auto;
		width: 100%;
		height: 100%;
		background: #000;
		position: relative;
		&:before {
			content: "";
			display: block;
			padding-top: 100%;
		}
	}

	.popup_text {
		margin-top: 20px;
		text-align: left;
		line-height: 1.35;
	}
}

.thanks_popup.myPopup {
	.center {
		padding-top: (138px/740*100);
		padding-bottom: (109px/740*100);
		padding-left: (86px/740*100);
		padding-right: (86px/740*100);
	}

	.h2_title {
		word-spacing: -0.2px;
	}
	
	max-width: 700px;
	.footer_btn {
		margin: 	0 auto;
		max-width: 105px;
	}

	.popup_description {
		margin-bottom: 	27px;
		font-size: 21px;
	}

	.popup_answer {
		
		font-size: 21px;
		max-width: 420px;
		margin: 	0 auto;
		margin-bottom: 	30px;
	}

}