.offer{
	position: relative;
	padding-top: 120px;
	.offer_card {

	}

	.flex-container {
		padding-top: 110px;
	}

	& > * {
		position: relative;
		z-index: 5;
	}

	.parallax-sawdust {
		position: absolute;
	}

	.sawdust-top {
		right: 0;
	}

	.sawdust-top-2 { 
		right: -5%; 
		top: 25%; 

	} 
	.sawdust-top-3 { 
		left: 5%; 
		top: 32%; 

	} 
	.sawdust-top-4 { 
		left: 40%; 
		top:50%; 

	} 

	.sawdust-bottom {
		bottom: 10%;
		left: 0;
		z-index: 3;
	}
}

.offer_card {
	.flex();
	position: relative;
	height: auto;
	margin-top: -90px;
}

.offer_cars-img {
	position: relative;
	top: 0;
	left: -28%;
	// box-shadow: 0px 0px 20px rgba(255,255,255,0.75);

}


.offer_card-reverse {
	flex-direction: row-reverse;
	.offer_cars-img {
		left: 27%;
	}

	.offer_card-text {
	flex: 1 0 37%;
	position: relative;
	left: 18%;
}
}

.offer_card-text {
	flex: 1 0 37%;
	position: relative;
	left: -18%;
}

.offer_cars-img {
	flex: 0 1 55%;
}

.offer_card-title {
	margin-bottom: 35px;
	font-size: 3.98vw;
	font-weight: 600;
}


.offer_card-desc {
	margin-bottom: 35px;
	letter-spacing: 0.45px;
