


@media screen and (min-width: 1280px) {
    
    .header-title {
        font-size: 51px;
    }

    .header_nav {
        font-size: 13px;
    }

    .header-subtitle {
        font-size: 30px;
    }

    .h2_title {
        font-size: 72px;
    }

    .section_subtitle {
        font-size: 18px;
    }

    .offer_card-title {
        font-size: 51px;

    }

    .offer_card-desc {
        font-size: 18px;
    }
    .objects_slider-title, .objects_slider-square span {
        font-size: 36px;
    }

    .objects_slider-square {
        font-size: 24px;
    }

    .why_card-text  {
        font-size: 18px;
    }

    .why_card-header {
        font-size: 30px;
    }

    .why .h2_title {
        font-size: 48px;
    }

    .contacts_h3, .popup_title.h2_title {
        font-size: 60px;
    }

    .contasts_tel, .contacts_mail {
        font-size: 30px;
    }




}

@media screen and (max-width: 1280px) {
        .offer_card-text {
            flex: 1 0 30%;
        }

        .offer_card-reverse .offer_card-text {
                flex: 1 0 30%;
        }
        .offer_cars-img {
            max-width: 85%;
        }
}

@media screen and (max-width: 1110px) {
    .header_tel {
        font-size: 20px;
    }

    .header_logo-wrap p {
        font-size: 42px
    }

    .header_logo-wrap small {
        font-size: 12px;
    }
}


@media screen and(max-width: 986px) {
     .section_subtitle {
        font-size: 14px;
    }

    .contacts {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,0.5);

        }
    }

    .why_card-text {
        font-size: 13px;
    }
    .header_nav-navigation a {
        font-size: 12px;
    }
    .header-btn, .main-btn {
        font-size: 15px;
        padding: 12px 45px;
    }

    .header_main-top {
        flex-direction: column;
    }

    .parallax-sawdust {
        max-width: 280px;
    }

    .text-field {
        padding: 10px 30px;
        height: 55px;
    }

    input.main-btn {
        height: 55px;
        padding: 10px 40px;
    }

    .calculator .flex-container .calc-form {
        flex-direction: column;
    }

    .calculator .flex-container {
        background-size: 150% 100%;
        max-height: 100%;
    }

    .offer_card-desc {
        font-size: 14px;
    }

    .objects_slider .slick-slide{
        margin: 0 50px;
    }
    .calc-form .select {
        margin-bottom: 15px;
    }
    .calculator .main-btn {
        width: 100%;
        max-width: 375px;
    }

    .offer_card-reverse .offer_card-text, .offer_card-text {
        flex: 1 0 35%;
    }

    .offer_card-reverse .offer_card-text {
        left: 23%;
    }

    .offer_card-text {
        left: -23%;
    } 

    .why {
        padding-top: 90px;
    }

    .offer .flex-container {
        padding-top: 70px;
    }
}

@media screen and(max-width: 760px) {
    .offer_card {
        flex-direction: column;
    }

    .calculator .flex-container .calc-form select, .calculator .main-btn {
        padding: 10px 25px;
        height: 60px;
        max-width: 325px;

    }

    .calculator .main-btn {
        line-height: 60px;
        padding: 0;
    }

    .calc-form .select:after {
        right: 30px;
        top: 30%;
    }

   .offer_card-title {
    margin-top:   15px;
    text-align: center;
   }

    .offer_cars-img, .offer_card-reverse .offer_cars-img {
        flex: none;
        max-width: 100%;
        left: 0;
    }

    .offer_card-text, .offer_card-reverse .offer_card-text {
        left: 0;
    }

    .offer_card {
        margin-top: 25px;
    }
    .offer_card-title {
        margin-bottom: 30px;
    }
    .offer_card-desc {
        margin-bottom: 25px;
    }

    .next, .prev {
        width: 80px;
        height: 80px;
        right: 0;

    }

    .next{
        left: 0;
    }
    
    .next svg, .prev svg {
        width: 55px;
        height: 55px;
    }

    .objects_slider-slide {
        padding: 0 15px 45px;
    }

   
    
    .why {
        .section_subtitle {
            text-align: center;
        }
    }

    .offer .flex-container {
        padding-top:  25px;
    }

    .why_card-header {
        font-size: 17px;
    }

    .next, .prev {
        top: 60%;
    }

    .contasts_tel {
        margin-bottom: 10px;
    }

    .contacts_form, .contacts_h3 {
        margin-bottom: 50px;
    }

    .contacts_wrapper {
        flex-direction: column;
    }

    .contacts_h3 {
        text-align: center;
    }




}



@media screen and(max-width: 640px) {
    .header_nav-navigation {
        flex-direction: column;
        width: 120%;
        position: absolute;
        z-index: 10;
        justify-content: center;
        background: #91d300;
        top: 40px;
        padding: 25px 0;
        color: #000;
        left: -10%;
        box-shadow: 10px 13px 9px #91d300;
    }

    .header_nav-link:before {
        background: #000;
    }

    .header_nav-link:last-child {
        margin-bottom:  0;
    }
    
    .contacts_wrap {
        display: block;
        text-align: center;
        .section_subtitle {
            text-align: center;
        }
    }
 

    .offer_card-title {
        font-size: 25px;
    }
    .h2_title {
        font-size: 37px;
        text-align: center;
    }
    .header_nav-link {
        margin-right: 0;
        margin-bottom: 15px;

    }
       .header-btn, .main-btn {
        font-size: 14px;
          padding: 10px 35px;

    }

    .offer {
        padding-top: 80px;
        .section_subtitle {
            margin-bottom: 10px;
        }
    }

    .header_nav {
        .flex-container {
            z-index: 10;
        }
    }

    .parallax-sawdust {
        display: none;
    }
    .burger-menu  {
         display: flex;
    }

    .header_nav-navigation {
        display: none;
    }

    .header_top-contacts {
        flex-direction: column;
    }

    .header_tel {
        margin-right: 0;
        margin-bottom: 15px;
    }

    .header_top-logo {
        margin-bottom: 15px;
    }

    .contacts_form {
        flex-flow: column;
        .text-field {
            margin-bottom: 15px;
            margin-right: 0;
        }
    }
    
    .header-buttons {
        flex-direction: column;
        .main-btn {
            width: 100%;
            max-width: 280px;
        }
    }

    .header-buttons .main-btn:first-child {
        margin-right: 0;
        margin-bottom:  15px;

    }

    .header-btn {
        width: 100%;
        min-width: 280px;
    }

    .header_logo-wrap p {
        font-size: 34px;
    }

    .logo {
        max-width: 60px;
    }

    .header_logo-wrap small {
        font-size: 10px;
    }

    .header-title {
        font-size: 25px;
    }
    .center {
        padding: 0 15px;
    }

    .header-subtitle { 
        font-size: 15px; 
        br{
            display: block;
        }

    }

    .offer_card-desc {
        font-size: 13px;
    }

    .next, .prev {
        width: 40px;
        height: 40px;
        right: 0;
        top: 70%;

    }

    .next{
        left: 0;
    }
    
    .next svg, .prev svg {
        width: 28px;
        height:28px;
    }

    .objects_slider .slick-slide{
        margin: 0 30px;
    }

    .section_subtitle {
        text-align: center;
    }

    .why .flex-container {
        flex-direction: column;
    }

    .why_card {
        max-width: 420px;
    }

    input.main-btn {
        max-width: 375px;
    }

    .why .h2_title {
        font-size: 24px;
    }

    .section_subtitle {
        margin-bottom: 35px;
    }

    .calculator {
        padding-top: 60px;
    }
    .objects_slider-title {
        font-size: 18px;
    }

    .objects_slider-square {
        font-size: 12px;
    }
    .contacts_h3 {
        font-size: 30px;
    }

    .contasts_tel, .contacts_mail {
        font-size: 15px;
    }

    .objects_slider-square span {
        font-size: 18px;
    }

    .slider-controls-item {
        font-size: 14px;
    }
   
}


@media screen and (max-width: 440px) {
      .header-title {
        font-size: 22px;
    }
    
    .next, .prev  {
        top: 75%;
    }
    .offer_card-title {
        font-size: 21px;
    } 

    .h2_title {
        font-size: 30px;
    }

    .objects_slider-slideHead {
        flex-direction: column;
    }

    .objects_slider-title {
        margin-bottom: 15px;
    }
    .offer {
        padding-top: 60px;
    }

    .why {
        padding-top: 60px;
    }

    .header_main {
        &:before {
            width: 100%;
            height: 100%;
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            background: rgba(0,0,0,0.4);
        }
    }
}