.objects {
	position: relative;
	padding-top: 10%;
	& > * {
		position: relative;
		z-index: 11;
	}
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 93.3%;
		background: url(../img/trees.png) no-repeat center;
		background-size: cover;
	}
	&:after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(255,255,255,0.85);
		
	}

	.center {
		text-align: center;
	}

	.parallax-sawdust {
		position: absolute;
	}

	.sawdust-top {

		top: -5%;
		right: 	0;
		z-index: 10;
	}
}

.objects_slider-controls {
	text-transform: uppercase;
	.flex();
	margin: 0 auto;
	padding: 4.68% 0;
	padding-top: 0;
}

.slider-controls-item {
	cursor: pointer;
	color: #9a9a9a;
	font-weight: 500;
	margin-right: 40px;
	font-size: 18px;
	&:last-child {
		margin-right: 0;
	}
}

.active-item {
	color: #78bf01;
	font-weight: 700;
}

.objects_slider {
	width: 100%;
	max-width: 100%;
	.slick-list {
		overflow: visible;
	}

	.slick-slide {
		margin: 0 70px;
	}
}

.objects_slider-slide {
	padding: 25px 25px;
	padding-bottom: 75px;
	padding-top: 0;
	background: #fff;
	-webkit-box-shadow: 0px 2px 26px -8px rgba(0,0,0,0.75);
-moz-box-shadow: 0px 2px 26px -8px rgba(0,0,0,0.75);
box-shadow: 0px 2px 26px -8px rgba(0,0,0,0.75);
}

.objects_slider-title {
	font-size: 2.812vw;
	font-weight: 500;
}

.objects_slider-square {
	font-size: 1.875vw;
	font-weight: 500;
	span {
		font-size: 2.812vw;
		color: #8ecf00;
	}
}

.objects_slider-slideHead {
	.flex();
	padding: 45px 0 30px 0;
}