.why_card {
	flex: 0 1 49.2%;
	.flex();
	margin-bottom: 	15px;
}

.why_card-text {
	letter-spacing: 0.25px;
	font-size: (18vw/1280*100);
	font-weight: 500;
}

.why_card-header {
	font-size: (30vw/1280*100);
	font-weight: 400;
}

.why_card-img {
	margin-right: 	8px;
	flex: 0 1 25%;
	img {
		margin: 0 auto;
	}
}

.why_card-content {
	flex: 0 1 75%;
}

.why {
	.parallax-sawdust {
		position: absolute;
	}

	.sawdust-bottom {
		left: 5%	;
		}
	position: relative;
	&>* {
		position: relative;
		z-index: 5;
	}
	padding-top: 120px;
	.h2_title {
		font-size: (48vw/1280*100);
		text-align: center;
	}

	.section_subtitle {
		text-align: center;

	}
}

.why_card-header {
	margin-bottom: 	10px;
}

