.calculator {
	position: relative;
	&> * {
		position: relative;
		z-index: 5;
	}
	padding-top: 80px;
	.flex-container {
		background: url(../img/board.jpg) no-repeat center;
		background-size: contain;
		justify-content: center;
		padding: 7% 3.5%;
		max-height: 345px;
		.calc-form {
			width: 	100%;
			.flex();
			select {
				width: 	100%;
				max-width: 375px;
				height: 	75px;
				text-align: center;
				padding: 15px 45px;
				text-transform: uppercase;
				font-family: 'Exo 2', sans-serif;
				font-size: 16px;
				background: #fff;

			}
		}
	}
	.main-btn {
		height: 70px;
		line-height: 70px;
		padding-top: 0;
		padding-bottom: 0;
	}

	.section_subtitle {
		text-align: center;	
	}

	.parallax-sawdust {
		position: absolute;
	}

	.sawdust-top {
		top: -3%;
		left: 	-15%;
	}

	.sawdust-top-2 {
		top: 10%;
		right: 	0;
	}
}

 .calculator {
        .h2_title, .section_subtitle {
            text-align: center;
        }
    }

.calc-form {
	margin-bottom: 45px;


	.select {
  position: relative;
  display: inline-block;
  width: 	100%;
	max-width: 375px;
	height: 	75px;
	text-align: center;
	margin: 0 15px;
					
	text-transform: uppercase;
	font-family: 'Exo 2', sans-serif;
	font-size: 16px;
}
.select:after {
  content: "â–¼";
  padding: 0 8px;
  font-size: 16px;
  position: absolute;
  right: 8px;
  top: 40%;
  z-index: 1;
  text-align: center;
  width: 10%;
  height: 100%;
  pointer-events: none;
  box-sizing: border-box;
}
select {
  padding-right: 25px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
}

