@import url('app/less/_fonts.less');


html, body {
	overflow-x: hidden; 

}
body 
{
	margin: 0;
	padding: 0;	
	font-family: 'Exo 2', sans-serif;
	font-size: 14px;
	font-weight: normal;
	color: #ffffff;
	letter-spacing: 1.15px;



}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {

	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
	color: #ffffff;
	cursor: pointer;
}


input {
	width: 100%;
	outline: none;
	border: none;
	text-align: center;
}


main {
	color: #000;
}

.center {
	max-width: 1330px;
	width: 100%;
	margin: 0 auto;
	padding: 0 25px;

}



.flex-container {

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
}


.flex(@justify-content: space-between, @align-items: center, @flex-direction: row, @flex-wrap: nowrap){
	display: flex;
	justify-content: @justify-content;
	align-items:  @align-items;
	flex-direction:  @flex-direction;
	flex-wrap:  @flex-wrap;
}

.header {
	min-height: 100%;
	// height: 100vh;
	width: 	100%;
	position: relative;
	&:before {
		content: "";
		position: absolute;
		width: 100%;
		height: 50%;
		background: #000;
		bottom: 0;
		left: 0;
		background: url(../img/trees.png) no-repeat top;
		background-size: cover;
	}
}


.header-title {
	font-size: 3.98vw;
	font-weight: 500;
	text-align: center;
	// letter-spacing: 0.04em;
	margin-bottom: 	30px;
}

.header-subtitle {
	text-align: center;
	// letter-spacing: 0.8px;
	font-size: (30vw/1280*100);
	br {
		display: none;
	}
}

.header_nav {
	height: 7%;

	height: 45px;
	background: #000;
	width: 	100%;
	text-transform: uppercase;

	font-size: 1.015vw;
	.center, .flex-container {
		height: 100%;
		position: relative;
	}

}

.header_main {
	height: 93%;
	background: url(../img/main_bg.jpg) no-repeat center;
	background-size: cover;
	width: 	100%;
	padding: 4% 0 6%;
	.center, .flex-container {
		height: 100%;
		& > * {
			position: relative;
			z-index: 3;
		}
	}
	.flex-container {
		flex-direction: column;
	}
}

.header_nav {
	.flex-container {
		justify-content: flex-end;
	}
}




.header_nav-navigation {
	.flex();

}

.header_top-logo {
	color: #000;
	.flex();
}

.header_main-top {
	.flex();
	width: 100%;
}

.header_top-contacts {
	.flex();
}

.header_nav-link {
	position: relative;
	

	
	&:before {
		content: "";
		position: absolute;
		width: 	0;
		height: 5px;
		bottom: -5px;
		left: 0;
		background: #91d300;
		transition: all 0.25s ease-in;	
		z-index: 2;	
	}

	&:hover:before {
		width: 100%;
	}
	margin-right: 45px;
	&:last-child {
		margin-right: 0;
	}
}

.header-buttons {
	.flex(@justify-content: center);	
	margin-top: 50px;
	.main-btn{
		&:first-child {
			margin-right: 45px;
		}
	}

}

.header_logo-wrap {
	margin-left: 25px;
	p {
		font-size: 48px;
		font-weight: 700;
	}
	small{
		font-weight: 500;
		font-size: 14px;
		letter-spacing: 0.5px;
		margin-top: 2px;
		display: block;
	}
}


.header_main-bottom {
	margin-top: 55%;
}


.header-btn {
	.main-btn();
	border: none;
}

.header_tel {
	color: #000;
	font-size: 24px;
	font-weight: 600;
	margin-right: 45px;
	span {
		color: #91d300;
	}
}

.header-subtitle {
	span {
		position: relative;
		&:before {
			position: absolute;
			content: "";
			width: 100%;
			height: 5px;
			background: #91d300;
			bottom: -8px;
			left: 0;
		}
	}
}

.section_subtitle {
	font-weight: 500;

}




















//POPUP


/* Styles for dialog window */
#small-dialog {
	background: white;
	padding: 20px 30px;
	text-align: left;
	max-width: 400px;
	margin: 40px auto;
	position: relative;
}


/**
 * Fade-zoom animation for first dialog
 */

/* start state */
.my-mfp-zoom-in .zoom-anim-dialog {
	opacity: 0;

	-webkit-transition: all 0.2s ease-in-out; 
	-moz-transition: all 0.2s ease-in-out; 
	-o-transition: all 0.2s ease-in-out; 
	transition: all 0.2s ease-in-out; 



	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 
}

/* animate in */
.my-mfp-zoom-in.mfp-ready .zoom-anim-dialog {
	opacity: 1;

	-webkit-transform: scale(1); 
	-moz-transform: scale(1); 
	-ms-transform: scale(1); 
	-o-transform: scale(1); 
	transform: scale(1); 
}

/* animate out */
.my-mfp-zoom-in.mfp-removing .zoom-anim-dialog {
	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 

	opacity: 0;
}

/* Dark overlay, start state */
.my-mfp-zoom-in.mfp-bg {
	opacity: 0;
	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-zoom-in.mfp-ready.mfp-bg {
	opacity: 0.75;
}
/* animate out */
.my-mfp-zoom-in.mfp-removing.mfp-bg {
	opacity: 0;
}

.mfp-close-btn-in .mfp-close {
	top: 7px;
	right: 15px;
	opacity: 1;
	color: transparent;
	&:hover:before {
		transform: rotate(180deg);
	}
	&:hover:after {
		transform: rotate(0deg);
	}

	&:before, &:after {
		content: "";
		position: absolute;
		width: 35px;
		height: 4px;
		background: #000;
		top: 50%;
		left:5px;
	}

	&:before {
		transform: rotate(135deg);
		transition: all 0.2s linear;
	}

	&:after {
		transform: rotate(45deg);
		transition: all 0.2s linear;
	}
}




/**
 * Fade-move animation for second dialog
 */

/* at start */
.my-mfp-slide-bottom .zoom-anim-dialog {
	opacity: 0;
	-webkit-transition: all 0.2s ease-out;
	-moz-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;

	-webkit-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-moz-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-ms-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-o-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );

}

/* animate in */
.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
	opacity: 1;
	-webkit-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-moz-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-ms-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-o-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
}

/* animate out */
.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
	opacity: 0;

	-webkit-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-moz-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-ms-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-o-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
}

/* Dark overlay, start state */
.my-mfp-slide-bottom.mfp-bg {
	opacity: 0;

	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-slide-bottom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
/* animate out */
.my-mfp-slide-bottom.mfp-removing.mfp-bg {
	opacity: 0;
}




// POPUP


.h2_title {
	font-size: 5.625vw;
	font-weight: 500;
	margin-bottom: 18px;

}

.section_subtitle{
	font-size: 1.406vw;
	letter-spacing: 0.35px;
	font-weight: 500;
	margin: 0 auto;
	margin-bottom: 50px;
	letter-spacing: 0.35px;
}

.main-btn {
	background: #000;
	border: 1px solid #91d300;
	text-align: center;
	text-transform: uppercase;
	width: auto;
	display: inline-block;
	padding: 14px 64px;
	font-size: 16px;
	cursor: pointer;
	transition: all 0.4s linear;	
	&:hover {
	background: #91d300;
	}
}

.parallax-sawdust {
	position: absolute;
}

.header {
	.sawdust-top{
		top: -3%;
		left: 0;
	}

	.sawdust-bottom {
		bottom: 0;
		right: 0;
	}
}

.next, .prev {
	cursor: pointer;
	position: absolute;
	z-index: 15;
	width: 120px;
	height: 120px;
	border-radius: 50%;
	background: #fff;
	top: 55%;
	.flex(@justify-content:center);
	svg {
		width: 85px;
		height: 85px;
		fill: #91d300;
	}
	
	
}

.prev {
		right: -35px;
	}

	.next {
		left: -35px;
	}




.burger-menu {

	cursor: pointer;
	overflow: hidden;
	width: 45px;
	height: 45px;
	position: absolute;
	transition: all 0.3s ease;
	top: 0;
	left: 0;
	.flex(@flex-direction: column, @justify-content: center);
	display: none;
	span {
		color: #fff;
		width: 35px;
		height: 2px !important;
		background: #fff;
		display: flex;
		margin-top: 8px;
		transition: all 0.29s cubic-bezier(0.68, -0.55, 0.27, 1.55);
		&:first-child {
			margin-top: 0;
		}
	}

}



.burger-active {
	span {
		margin: 0;
	}
	span:first-child {
		transform: rotate(45deg) translate(2px);

	
	}

	span:nth-child(2) {
		transform: translateX(-125%);
		
	}

	span:last-child {
		transform: rotate(-45deg) translate(2px);
		
	}
}














